@import '../variables/colors.scss';
@import '../variables/textStyles.scss';

.todaywordcard-background {
    display: flex;
    width: calc(100vw - 40px);
    padding: 16px;
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;

    border-radius: 16px;
    background-color: $koreasy-color-gray-06;

    .wordInfo-frame {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        align-self: stretch;
        word-break: break-all;

        color: $koreasy-color-gray-00;
        @include body01-medium;

        .mean {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            gap: 4px;

            .text-box {
                display: flex;
                align-items: center;
                gap: 8px;

                .krWord {
                    color: $koreasy-color-primary;
                    @include title01-heavy;
                }

                .sound-box {
                    display: flex;
                    width: 24px;
                    height: 32px;
                    padding: 10px;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    gap: 10px;
                }
            }
        }

        .bookmark-box {
            display: flex;

            padding: 10px;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            gap: 10px;
        }
    }

    .example-frame {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 8px;
        align-self: stretch;

        color: $koreasy-color-primary;
        @include body01-bold;
    }

    .option-frame {
        display: flex;
        padding: 0 24px;
        justify-content: space-between;
        align-items: flex-start;
        align-self: stretch;

        .option-buttion {
            display: flex;
            height: 40px;
            padding: 8px 16px;
            justify-content: center;
            align-items: center;
            gap: 4px;
            white-space: pre;

            color: $koreasy-color-gray-00;
            @include body01-medium;

            .icon-box {
                display: flex;
                width: 24px;
                height: 32px;
                padding: 10px;
                justify-content: center;
                align-items: center;
                gap: 10px;
            }
        }
    }
}
